import React, { useState } from 'react';
import '../../styles/faqs/faqs.css';

const FAQs = () => {
    const [activeIndex, setActiveIndex] = useState(null);
  
    const toggleAnswer = (index) => {
      setActiveIndex(activeIndex === index ? null : index);
    };
  
    const faqs = [
      {
        question: 'Qu incluye el servicio de Posicionamiento SEO?',
        answer: 'Nuestro servicio de Posicionamiento SEO optimiza tu sitio web para mejorar su visibilidad en los motores de bsqueda. Incluye anlisis de palabras clave, optimizacin on-page, creacin de contenido y construccin de enlaces.'
      },
      {
        question: 'Qu aspectos cubre el Diseo Web?',
        answer: 'El Diseo Web abarca desde la creacin de un sitio atractivo y funcional hasta la adaptacin para todos los dispositivos. Tambin incluimos el diseo de la interfaz y la experiencia del usuario (UI/UX).'
      },
      {
        question: 'Qu es el Copywriting y cmo puede beneficiar a mi negocio?',
        answer: 'El Copywriting es la redaccin de contenido persuasivo que comunica efectivamente con tu audiencia. Ayuda a mejorar la imagen de tu marca, aumentar la conversin y fortalecer la relacin con tus clientes.'
      },
      {
        question: 'Qu tipo de mantenimiento de webs ofrecen?',
        answer: 'Ofrecemos mantenimiento continuo que incluye actualizaciones de seguridad, correccin de errores, mejoras en el rendimiento y ajustes necesarios para mantener tu sitio funcionando sin problemas.'
      },
      {
        question: 'Cmo funciona el servicio de Email Marketing?',
        answer: 'Desarrollamos campaas de email marketing personalizadas que incluyen diseo de correos, segmentacin de audiencias y anlisis de resultados para maximizar el impacto de tus comunicaciones con los clientes.'
      },
      {
        question: 'Qu incluye el servicio de Diseo Grfico?',
        answer: 'El servicio de Diseo Grfico abarca la creacin de materiales visuales como flyers, logotipos y otros elementos grficos. Nos enfocamos en disear contenidos atractivos y personalizados que representen tu marca de manera profesional.'
      },
      {
        question: 'Puedo contratar un solo servicio o necesito contratar todos?',
        answer: 'Pods contratar nuestros servicios de manera individual segn tus necesidades especficas, o elegir un paquete completo para un enfoque ms integral y personalizado en el desarrollo de tu presencia online.'
      },
      {
        question: 'Cmo puedo solicitar un servicio?',
        answer: 'Para solicitar un servicio, simplemente contactanos especificando tus necesidades generales. Nos pondremos en contacto contigo para analizar tu proyecto y seguir con el proceso de manera eficiente.'
      }
    ];
    
  
    return (
      <section id="faqs">
        <div className="faqs-header">
          <h2>Preguntas Frecuentes</h2>
          <p>Resolvemos las dudas ms comunes sobre nuestros servicios para que puedas tomar decisiones informadas.</p>
        </div>
        <div className="faqs-list">
          {faqs.map((faq, index) => (
            <div className="faq-item" key={index}>
              <div 
                className="faq-question" 
                onClick={() => toggleAnswer(index)}
              >
                <h4>{faq.question}</h4>
                <span className="toggle-icon">
                  {activeIndex === index ? '' : '+'}
                </span>
              </div>
              <div className={`faq-answer ${activeIndex === index ? 'show' : ''}`}>
                <p>{faq.answer}</p>
              </div>
            </div>
          ))}
        </div>
      </section>
    );
  };
  
  export default FAQs;